import { Component, OnInit, Inject, ElementRef, ViewChild } from '@angular/core';
import { FormGroup, FormBuilder, Validators, ValidatorFn, AbstractControl } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MtxDialogData } from '@ng-matero/extensions';
import { ClientService } from 'app/shared/services/client/client.service';
import { FormControl, FormArray } from '@angular/forms';
import { MasterService } from 'app/shared/services/master/master.service';
import { Router } from '@angular/router';
import { ChargingModelService } from 'app/shared/services/charging-model/charging-model.service';
import { PATTERNS } from 'app/shared/utils/validation-patterns';
import { ChargerTypeService } from 'app/shared/services/charger-type/charger-type.service';
import { Observable } from 'rxjs';
import { map, startWith, tap } from 'rxjs/operators';
import { MessageConstant } from 'app/shared/utils/message.constant';
import { ViewEncapsulation } from '@angular/core';
import { fuseAnimations } from '@fuse/animations';
export interface ChargingModel {
  id?: number;
  client_id?: number;
  charger_type_id: number;
  client_name?: string;
  manufacturer_id: number;
  manufacturer_name?: string;
  charger_model_type_id: number;
  charger_model_type_name?: string;
  battery_backup: string;
  isDual: number;
  code: string;
  name: string;
  description: string;
  communication_protocol_id: number;
  communication_protocol_name?: string;
  communication_mode: string;
  card_reader_type: string;
  no_of_connectors: number;
  connector_data: ChargingModelConnectorMap[];
  status: string;
  created_date?: string;
  created_by?: number;
  modify_date?: string;
  modify_by?: number;
}

export interface ChargingModelConnectorMap {
  id: number;
  model_id: number;
  connector_type_id: number;
  io_type_id: number;
  current_type_id: number;
  voltage: number;
  phase: number;
  max_amp: number;
  power: number;
  frequency: number;
  status: string;
  created_date: string;
  created_by: number;
  modify_date: string;
  modify_by: number;
}
function autocompleteObjectValidator(): ValidatorFn {
  return (control: AbstractControl): { [key: string]: any } | null => {
    if (typeof control.value === 'string') {
      return { 'invalidAutocompleteObject': { value: control.value } }
    }
    return null  /* valid option selected */
  }
}
@Component({
  selector: 'app-action-dialog',
  templateUrl: './action-dialog.component.html',
  styleUrls: ['./action-dialog.component.scss'],
  encapsulation: ViewEncapsulation.None,
  animations: fuseAnimations
})
export class ActionDialogComponent implements OnInit {

  params: any;
  isEditMode = false;
  form: FormGroup;
  localStorage: any;

  chargerTypes: any;
  manufacturers: any;
  chargerModelTypes: any;
  communicationProtocols: any;
  connectorTypes: any;
  ioTypes: any;
  currentTypes: any;
  batteryBackup = false;
  rfidCardReader = false;
  isDualCardReader: boolean = false;
  status = false;
  @ViewChild("toggleElement") ref: any;
  @ViewChild("rfidToggleElement") rfid: any;
  @ViewChild("isDualToggleElement") isDual: any;
  @ViewChild("activeElement") active: any;
  filteredOptionsCharType: Observable<any[]>;
  filteredOptionsManufacturer: Observable<any[]>;
  filteredOptionsCharModelType: Observable<any[]>;
  filteredOptionsCommunicationProto: Observable<any[]>;
  communicationModeData: string[] = ['LAN', 'SIM', 'WIFI'];
  // messageConstant = MessageConstant;
  constructor(private fb: FormBuilder, private chargerTypeService: ChargerTypeService,
    private snackBar: MatSnackBar, public dialogRef: MatDialogRef<ActionDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: MtxDialogData,
    private chargingModelService: ChargingModelService,
    private masterService: MasterService,
    private router: Router) { }

  ngOnInit() {
    this.initialize();
  }

  initialize() {

    this.localStorage = JSON.parse(localStorage.getItem('userDetails'));

    this.params = this.data;

    this.isEditMode = this.params.action == 'CREATE' ? false : true;

    this.localStorage = JSON.parse(localStorage.getItem('userDetails'));

    this.form = this.fb.group({
      chargerType: ['', [autocompleteObjectValidator(), Validators.required]],
      manufacturer: ['', [autocompleteObjectValidator(), Validators.required]],
      chargerModelType: ['', [autocompleteObjectValidator(), Validators.required]],
      batteryBackup: [{ value: false }, [Validators.required]],
      isDualCardReader: this.isDualCardReader == true ? 1 : 0,
      code: ['', [Validators.pattern(/^[a-zA-Z0-9]*$/)]],
      name: ['', [Validators.required, Validators.pattern(/^[a-zA-Z0-9 ]*$/)]],
      description: ['', [Validators.pattern(/^[a-zA-Z0-9 ]*$/)]],
      communicationProtocol: ['', [autocompleteObjectValidator(), Validators.required]],
      communicationMode: ['', [Validators.required]],
      cardReaderType: [''],
      noOfConnectors: [{ value: '', disabled: true }, [Validators.required, Validators.pattern(PATTERNS.ONLY_NUMBERS)]],
      connectorForm: this.fb.array([this.createConnectorFG()]),
    });

    this.getChargetTypes();
    this.getManufacturers();
    this.getChargerModelTypes();
    this.getCommunicationProtocols();
    this.getConnectorTypes();
    this.getIOTypes();
    this.getCurrentTypes();
    this.form.get('noOfConnectors').setValue(this.form.value.connectorForm.length);


    if (this.isEditMode) {
      this.assignValues(this.params);
    }
    this.filteredOptions();
  }

  private createConnectorFG(): FormGroup {
    return new FormGroup({
      connectorType: new FormControl(''),
      ioType: new FormControl(''),
      currentType: new FormControl('',),
      voltage: new FormControl('', [Validators.pattern(PATTERNS.THREE_DIGIT_DECIMAL)]),
      phase: new FormControl('', [Validators.pattern(PATTERNS.THREE_DIGIT_DECIMAL)]),
      maxAmp: new FormControl('', [Validators.pattern(PATTERNS.THREE_DIGIT_DECIMAL)]),
      power: new FormControl('', [Validators.pattern(PATTERNS.THREE_DIGIT_DECIMAL)]),
      frequency: new FormControl('', [Validators.pattern(PATTERNS.THREE_DIGIT_DECIMAL)])
    });
  }

  public addConnectorFG() {
    const connectorForm = this.form.get('connectorForm') as FormArray
    connectorForm.push(this.createConnectorFG());
    this.form.get('noOfConnectors').setValue(this.form.value.connectorForm.length);

  }

  public removeConnectorFG(i: number) {
    const connectorForm = this.form.get('connectorForm') as FormArray
    if (connectorForm.length > 1) {
      connectorForm.removeAt(i)
    } else {
      connectorForm.reset()
    }

    this.form.get('noOfConnectors').setValue(this.form.value.connectorForm.length);

  }

  getChargetTypes() {
    this.chargerTypeService.getActiveChargerTypes().subscribe(
      (resp: any) => {
        if (resp.status == true) {
          if (resp.data.length > 0) {
            this.chargerTypes = resp.data;

            if (this.isEditMode) {
              const toSelect = this.chargerTypes.find(c => c.id == this.params.data.charger_type_id);
              this.form.get('chargerType').setValue(toSelect);
              this.filteredOptionsCharType = this.form.controls.chargerType.valueChanges
                .pipe(
                  startWith(''),
                  //tap(() => this.form.controls.chargerType.setValue(toSelect)),
                  //tap(() => this.form.get('chargerType').setValue({name:toSelect.name, id:toSelect.id})),
                  map(value => typeof value === 'string' ? value : value.name),
                  map(name => name ? this._filterCharType(name) : this.chargerTypes)

                );
            }
          } else {
            this.snackBar.open(MessageConstant.NoRecordFoundMessage, 'Dismiss', { duration: 2000 });
          }
        }


      }, (err) => {
      }
    )
  }

  getManufacturers() {
    this.masterService.getManufacturers().subscribe(
      (data: any) => {
        this.manufacturers = data;
        if (this.isEditMode) {
          const toSelect = this.manufacturers.find(c => c.id == this.params.data.manufacturer_id);
          this.form.get('manufacturer').setValue(toSelect);
          this.filteredOptionsManufacturer = this.form.controls.manufacturer.valueChanges
            .pipe(
              startWith(''),
              // tap(() => this.form.controls.manufacturer.setValue(toSelect)),
              map(value => typeof value === 'string' ? value : value.name),
              map(name => name ? this._filterManfacturer(name) : this.manufacturers)
            );
        }
      }, (err) => {
      }
    )
  }
  getChargerModelTypes() {
    this.masterService.getChargerModelTypes().subscribe(
      (data: any) => {
        this.chargerModelTypes = data;
        if (this.isEditMode) {
          const toSelect = this.chargerModelTypes.find(c => c.id == this.params.data.charger_model_type_id);
          this.form.get('chargerModelType').setValue(toSelect);
          this.filteredOptionsCharModelType = this.form.controls.chargerModelType.valueChanges
            .pipe(
              startWith(''),
              //tap(() => this.form.controls.chargerModelType.setValue(toSelect)),
              map(value => typeof value === 'string' ? value : value.name),
              map(name => name ? this._filterCharModelType(name) : this.chargerModelTypes)
            );
        }
      }, (err) => {
      }
    )
  }
  getCommunicationProtocols() {
    this.masterService.getCommunicationProtocols().subscribe(
      (data: any) => {
        this.communicationProtocols = data;
        if (this.isEditMode) {

          const toSelect = this.communicationProtocols.find(c => c.id == this.params.data.communication_protocol_id);
          this.form.get('communicationProtocol').setValue(toSelect);
          this.filteredOptionsCommunicationProto = this.form.controls.communicationProtocol.valueChanges
            .pipe(
              startWith(''),
              //tap(() => this.form.controls.communicationProtocol.setValue(toSelect)),
              map(value => typeof value === 'string' ? value : value.name),
              map(name => name ? this._filterCommunicationProtocol(name) : this.communicationProtocols)
            );
        }
      }, (err) => {
      }
    )
  }

  getConnectorTypes() {
    this.masterService.getConnectorTypes().subscribe(
      (data: any) => {

        this.connectorTypes = data;
        // if(this.isEditMode){
        //   
        //   const toSelect = this.manufacturers.find(c => c.id == this.params.data.manufacturer_id);
        //   this.form.get('manufacturer').setValue(toSelect.id);
        // }
      }, (err) => {
      }
    )
  }
  getIOTypes() {
    this.masterService.getIOTypes().subscribe(
      (data: any) => {
        this.ioTypes = data;
        // if(this.isEditMode){
        //   
        //   const toSelect = this.manufacturers.find(c => c.id == this.params.data.manufacturer_id);
        //   this.form.get('manufacturer').setValue(toSelect.id);
        // }
      }, (err) => {
      }
    )
  }
  getCurrentTypes() {
    this.masterService.getCurrentTypes().subscribe(
      (data: any) => {
        this.currentTypes = data;
        // if(this.isEditMode){
        //   
        //   const toSelect = this.manufacturers.find(c => c.id == this.params.data.manufacturer_id);
        //   this.form.get('manufacturer').setValue(toSelect.id);
        // }
      }, (err) => {
      }
    )
  }

  close() {

    this.dialogRef.close();
  }
  closeActionPanel() {
    this.dialogRef.close();
  }

  onYesClick() {
    if (this.form.valid) {
      let formValues = this.form.value;
      let connectors = [];

      formValues.connectorForm.forEach(item => {
        connectors.push({
          connector_type_id: item.connectorType,
          io_type_id: item.ioType,
          current_type_id: item.currentType,
          voltage: item.voltage,
          phase: item.phase,
          max_amp: item.maxAmp,
          power: item.power,
          frequency: item.frequency,
          status: 'Y',
          created_by: this.localStorage.userId
        })
      });

      let params: ChargingModel = {
        charger_type_id: formValues.chargerType.id,
        manufacturer_id: formValues.manufacturer.id,
        charger_model_type_id: formValues.chargerModelType.id,
        battery_backup: this.batteryBackup == true ? 'Y' : 'N',
        isDual: this.isDualCardReader == true ? 1 : 0,
        code: formValues.code,
        name: formValues.name,
        description: formValues.description,
        communication_protocol_id: formValues.communicationProtocol.id,
        communication_mode: formValues.communicationMode,
        card_reader_type: formValues.cardReaderType,
        no_of_connectors: formValues.connectorForm.length,
        connector_data: connectors,
        status: this.status == true ? 'Y' : 'N',
        created_by: this.localStorage.userId
      }

      if (this.isEditMode) {
        params.id = this.params.data.id,
          params.modify_by = this.localStorage.userId;
      } else {
        params.created_by = this.localStorage.userId;
      }

      if (this.isEditMode) {
        this.chargingModelService.update(params).subscribe(
          (data: any) => {

            this.snackBar.open(MessageConstant.recordUpdateMessage, 'Dismiss', { duration: 5000 });
            this.dialogRef.close();
          },
          (err) => {

            this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 5000 });
          }
        )
      } else {
        this.chargingModelService.create(params).subscribe(
          (data: any) => {

            this.snackBar.open(MessageConstant.chargerCreateMessage, 'Dismiss', { duration: 5000 });
            // this.router.navigate(['/chargingModel/crud/list']);
            this.dialogRef.close();
          },
          (err) => {

            this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 5000 });
          }
        )

      }

      this.dialogRef.close(params);
    }

    return;
  }

  getErrorMessage(form: FormGroup) {
    return form.get('email').hasError('required')
      ? 'You must enter a value'
      : form.get('email').hasError('email')
        ? 'Not a valid email'
        : '';
  }

  assignValues(params) {
    this.form.get('code').setValue(params.data.code);
    this.form.get('name').setValue(params.data.name);
    this.form.get('description').setValue(params.data.description);
    var temp = new Array();
    temp = this.params.data.communication_mode.split(",");

    this.form.get('communicationMode').setValue(temp);
    this.form.get('cardReaderType').setValue(params.data.card_reader_type);
    this.form.get('noOfConnectors').setValue(params.data.no_of_connectors);

    this.status = params.data.status == 'Y' ? true : false;
    this.batteryBackup = params.data.battery_backup == 'Y' ? true : false;
    this.isDualCardReader = params.data.isDual == 1 ? true : false;
    this.assignValuesFG(params.data.connector_data);
  }

  assignValuesFG(data) {

    let connector_data = [];

    data.forEach(connector => {
      this.addConnectorFG();
      connector_data.push({
        id: connector.id,
        model_id: connector.model_id,
        connectorType: connector.connector_type_id,
        ioType: connector.io_type_id,
        currentType: connector.current_type_id,
        voltage: connector.voltage,
        phase: connector.phase,
        maxAmp: connector.max_amp,
        power: connector.power,
        frequency: connector.frequency,
        status: connector.status,
        created_date: connector.created_date,
        created_by: connector.created_by,
        modify_date: connector.modify_date,
        modify_by: connector.modify_by
      })
    });

    this.removeConnectorFG(data.length);

    const connectorForm = this.form.get('connectorForm') as FormArray;

    connectorForm.patchValue(connector_data);


  }

  clkBatteryBackup(event) {
    this.batteryBackup = event.checked;
    this.ref.checked = this.batteryBackup;
  }
  clkRfidCardReader(event) {
    this.rfidCardReader = event.checked;
    this.rfid.checked = this.rfidCardReader;
  }
  clkIsDualCardReader(event) {
    this.isDualCardReader = event.checked;
    this.isDual.checked = this.isDualCardReader;
  }
  updateStatus(event) {
    this.status = event.checked;
    this.active.checked = this.status;
  }

  filteredOptions() {
    this.filteredOptionsCharType = this.form.controls.chargerType.valueChanges
      .pipe(
        startWith(''),
        map(value => typeof value === 'string' ? value : value.name),
        map(name => name ? this._filterCharType(name) : this.chargerTypes)
      );
    this.filteredOptionsManufacturer = this.form.controls.manufacturer.valueChanges
      .pipe(
        startWith(''),

        map(value => typeof value === 'string' ? value : value.name),
        map(name => name ? this._filterManfacturer(name) : this.manufacturers)
      );
    this.filteredOptionsCharModelType = this.form.controls.chargerModelType.valueChanges
      .pipe(
        startWith(''),
        map(value => typeof value === 'string' ? value : value.name),
        map(name => name ? this._filterCharModelType(name) : this.chargerModelTypes)
      );
    this.filteredOptionsCommunicationProto = this.form.controls.communicationProtocol.valueChanges
      .pipe(
        startWith(''),
        map(value => typeof value === 'string' ? value : value.name),
        map(name => name ? this._filterCommunicationProtocol(name) : this.communicationProtocols)
      );
  }
  displayCharType(item: any): string {
    return item && item.name ? item.name : '';
  }
  displayManfacturer(item: any): string {
    return item && item.name ? item.name : '';
  }
  displayCharModelType(item: any): string {
    return item && item.name ? item.name : '';
  }
  displayCommunicationProto(item: any): string {
    return item && item.name ? item.name : '';
  }
  private _filterCharType(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.chargerTypes.filter(option => option.name.toLowerCase().includes(filterValue));
  }
  private _filterManfacturer(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.manufacturers.filter(option => option.name.toLowerCase().includes(filterValue));
  }
  private _filterCharModelType(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.chargerModelTypes.filter(option => option.name.toLowerCase().includes(filterValue));
  }
  private _filterCommunicationProtocol(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.communicationProtocols.filter(option => option.name.toLowerCase().includes(filterValue));
  }

}










