import { Component, OnInit, ViewChild, ElementRef } from "@angular/core";
import {
    Validators,
    FormBuilder,
    FormGroup,
    FormControl,
    FormArray,
    ValidatorFn,
    AbstractControl,
} from "@angular/forms";
import { MasterService } from "app/shared/services/master/master.service";
import { MatSnackBar } from "@angular/material/snack-bar";
import { Router } from "@angular/router";
import { ChargingModelService } from "app/shared/services/charging-model/charging-model.service";
import { PATTERNS } from "app/shared/utils/validation-patterns";
import { ChargerTypeService } from "app/shared/services/charger-type/charger-type.service";
import { Observable } from "rxjs";
import { map, startWith } from "rxjs/operators";
import { MessageConstant } from "app/shared/utils/message.constant";
export interface ChargingModel {
    id?: number;
    client_id?: number;
    charger_type_id: number;
    client_name?: string;
    manufacturer_id: number;
    manufacturer_name?: string;
    charger_model_type_id: number;
    charger_model_type_name?: string;
    battery_backup: string;
    isDual: number;
    code: string;
    name: string;
    description: string;
    communication_protocol_id: number;
    communication_protocol_name?: string;
    communication_mode: string;
    card_reader_type: string;
    no_of_connectors: number;
    connector_data: ChargingModelConnectorMap[];
    status: string;
    created_date?: string;
    created_by?: number;
    modify_date?: string;
    modify_by?: number;
}

export interface ChargingModelConnectorMap {
    id: number;
    model_id: number;
    connector_type_id: number;
    io_type_id: number;
    current_type_id: number;
    voltage: number;
    phase: number;
    max_amp: number;
    power: number;
    frequency: number;
    status: string;
    created_date: string;
    created_by: number;
    modify_date: string;
    modify_by: number;
}
function autocompleteObjectValidator(): ValidatorFn {
    return (control: AbstractControl): { [key: string]: any } | null => {
        if (typeof control.value === "string") {
            return { invalidAutocompleteObject: { value: control.value } };
        }
        return null; /* valid option selected */
    };
}
@Component({
    selector: "app-create",
    templateUrl: "./create.component.html",
    styleUrls: ["./create.component.scss"],
})
export class CreateComponent implements OnInit {
    form: FormGroup;

    chargerTypes: any;
    manufacturers: any;
    chargerModelTypes: any;
    communicationProtocols: any;
    connectorTypes: any;
    ioTypes: any;
    currentTypes: any;
    localStorage: any;
    batteryBackup = false;
    rfidCardReader = false;
    isDualCardReader: boolean = false;
    status = false;
    @ViewChild("toggleElement") ref: any;
    @ViewChild("rfidToggleElement") rfid: any;
    @ViewChild("isDualToggleElement") isDual: any;
    @ViewChild("activeElement") active: any;
    filteredOptionsCharType: Observable<any[]>;
    filteredOptionsManufacturer: Observable<any[]>;
    filteredOptionsCharModelType: Observable<any[]>;
    filteredOptionsCommunicationProto: Observable<any[]>;
    filteredOptionsConnectorType: Observable<any[]>;
    filteredOptionsIoType: Observable<any[]>;
    filteredOptionsCurrentType: Observable<any[]>;
    communicationModeData: string[] = ["LAN", "SIM", "WIFI"];
    //messageConstant = MessageConstant;
    constructor(
        private fb: FormBuilder,
        private chargingModelService: ChargingModelService,
        private chargerTypeService: ChargerTypeService,
        private masterService: MasterService,
        private snackBar: MatSnackBar,
        private router: Router
    ) {
        this.batteryBackup = false;
    }

    ngOnInit() {
        this.initialize();
    }

    initialize() {
        this.localStorage = JSON.parse(localStorage.getItem("userDetails"));
        const fgroup = this.createConnectorFG();

        this.form = this.fb.group({
            chargerType: [
                "",
                [autocompleteObjectValidator(), Validators.required],
            ],
            manufacturer: [
                "",
                [autocompleteObjectValidator(), Validators.required],
            ],
            chargerModelType: [
                "",
                [autocompleteObjectValidator(), Validators.required],
            ],
            batteryBackup: [{ value: false }, [Validators.required]],
            isDualCardReader: this.isDualCardReader == true ? 1 : 0,
            code: ["", [Validators.pattern(/^[a-zA-Z0-9]*$/)]],
            name: [
                "",
                [Validators.required, Validators.pattern(/^[a-zA-Z0-9 ]*$/)],
            ],
            description: ["", [Validators.pattern(/^[a-zA-Z0-9 ]*$/)]],
            communicationProtocol: [
                "",
                [autocompleteObjectValidator(), Validators.required],
            ],
            communicationMode: [
                "",
                [autocompleteObjectValidator(), Validators.required],
            ],
            cardReaderType: [""],
            noOfConnectors: [
                { value: "", disabled: true },
                [
                    Validators.required,
                    Validators.pattern(PATTERNS.ONLY_NUMBERS),
                ],
            ],
            connectorForm: this.fb.array([fgroup]),
            status: this.status == true ? "Y" : "N",
        });

        this.getChargerTypes();
        this.getManufacturers();
        this.getChargerModelTypes();
        this.getCommunicationProtocols();
        this.getConnectorTypes(fgroup);
        this.getIOTypes(fgroup);
        this.getCurrentTypes(fgroup);
        this.form
            .get("noOfConnectors")
            .setValue(this.form.value.connectorForm.length);
        this.filteredOptions();
    }

    private createConnectorFG(): FormGroup {
        return new FormGroup({
            connectorType: new FormControl(""),
            ioType: new FormControl(""),
            currentType: new FormControl(""),
            voltage: new FormControl("", [
                Validators.pattern(PATTERNS.THREE_DIGIT_DECIMAL),
            ]),
            phase: new FormControl("", [
                Validators.pattern(PATTERNS.THREE_DIGIT_DECIMAL),
            ]),
            maxAmp: new FormControl("", [
                Validators.pattern(PATTERNS.THREE_DIGIT_DECIMAL),
            ]),
            power: new FormControl("", [
                Validators.pattern(PATTERNS.THREE_DIGIT_DECIMAL),
            ]),
            frequency: new FormControl("", [
                Validators.pattern(PATTERNS.THREE_DIGIT_DECIMAL),
            ]),
        });
    }

    public addConnectorFG() {
        const connectorForm = this.form.get("connectorForm") as FormArray;
        const fgroup = this.createConnectorFG();
        connectorForm.push(fgroup);
        this.filteredOptionsConnectorType =
            fgroup.controls.connectorType.valueChanges.pipe(
                startWith(""),
                map((value) =>
                    typeof value === "string" ? value : value.name
                ),
                map((name) =>
                    name
                        ? this._filterConnectorType(name)
                        : this.connectorTypes.slice()
                )
            );
        this.filteredOptionsIoType = fgroup.controls.ioType.valueChanges.pipe(
            startWith(""),
            map((value) => (typeof value === "string" ? value : value.name)),
            map((name) => (name ? this._filterIoType(name) : this.ioTypes))
        );
        this.filteredOptionsCurrentType =
            fgroup.controls.currentType.valueChanges.pipe(
                startWith(""),
                map((value) =>
                    typeof value === "string" ? value : value.name
                ),
                map((name) =>
                    name ? this._filterCurrentType(name) : this.currentTypes
                )
            );

        this.form
            .get("noOfConnectors")
            .setValue(this.form.value.connectorForm.length);
    }

    public removeConnectorFG(i: number) {
        const connectorForm = this.form.get("connectorForm") as FormArray;
        if (connectorForm.length > 1) {
            connectorForm.removeAt(i);
        } else {
            connectorForm.reset();
        }

        this.form
            .get("noOfConnectors")
            .setValue(this.form.value.connectorForm.length);
    }

    getChargerTypes() {
        //;
        this.chargerTypeService.getActiveChargerTypes().subscribe(
            (resp: any) => {
                //;
                if (resp.status == true) {
                    if (resp.data.length > 0) {
                        this.chargerTypes = resp.data;
                        this.filteredOptionsCharType =
                            this.form.controls.chargerType.valueChanges.pipe(
                                startWith(""),
                                map((value) =>
                                    typeof value === "string"
                                        ? value
                                        : value.name
                                ),
                                map((name) =>
                                    name
                                        ? this._filterCharType(name)
                                        : this.chargerTypes
                                )
                            );
                    } else {
                        this.snackBar.open(
                            MessageConstant.NoRecordFoundMessage,
                            "Dismiss",
                            { duration: 2000 }
                        );
                    }
                }
            },
            (err) => {
                //;
            }
        );
    }

    getManufacturers() {
        this.masterService.getManufacturers().subscribe(
            (data: any) => {
                this.manufacturers = data;
                this.filteredOptionsManufacturer =
                    this.form.controls.manufacturer.valueChanges.pipe(
                        startWith(""),
                        map((value) =>
                            typeof value === "string" ? value : value.name
                        ),
                        map((name) =>
                            name
                                ? this._filterManfacturer(name)
                                : this.manufacturers
                        )
                    );
            },
            (err) => {}
        );
    }

    getChargerModelTypes() {
        this.masterService.getChargerModelTypes().subscribe(
            (data: any) => {
                this.chargerModelTypes = data;
                this.filteredOptionsCharModelType =
                    this.form.controls.chargerModelType.valueChanges.pipe(
                        startWith(""),
                        map((value) =>
                            typeof value === "string" ? value : value.name
                        ),
                        map((name) =>
                            name
                                ? this._filterCharModelType(name)
                                : this.chargerModelTypes
                        )
                    );
            },
            (err) => {}
        );
    }

    getCommunicationProtocols() {
        this.masterService.getCommunicationProtocols().subscribe(
            (data: any) => {
                this.communicationProtocols = data;
                this.filteredOptionsCommunicationProto =
                    this.form.controls.communicationProtocol.valueChanges.pipe(
                        startWith(""),
                        map((value) =>
                            typeof value === "string" ? value : value.name
                        ),
                        map((name) =>
                            name
                                ? this._filterCommunicationProtocol(name)
                                : this.communicationProtocols
                        )
                    );
            },
            (err) => {}
        );
    }

    getConnectorTypes(fgroup: FormGroup) {
        this.masterService.getConnectorTypes().subscribe(
            (data: any) => {
                this.connectorTypes = data;
                this.filteredOptionsConnectorType =
                    fgroup.controls.connectorType.valueChanges.pipe(
                        startWith(""),
                        map((value) =>
                            typeof value === "string" ? value : value.name
                        ),
                        map((name) =>
                            name
                                ? this._filterConnectorType(name)
                                : this.connectorTypes
                        )
                    );
            },
            (err) => {}
        );
    }

    getIOTypes(fgroup: FormGroup) {
        this.masterService.getIOTypes().subscribe(
            (data: any) => {
                this.ioTypes = data;
                this.filteredOptionsIoType =
                    fgroup.controls.ioType.valueChanges.pipe(
                        startWith(""),
                        map((value) =>
                            typeof value === "string" ? value : value.name
                        ),
                        map((name) =>
                            name ? this._filterIoType(name) : this.ioTypes
                        )
                    );
            },
            (err) => {}
        );
    }

    getCurrentTypes(fgroup: FormGroup) {
        this.masterService.getCurrentTypes().subscribe(
            (data: any) => {
                this.currentTypes = data;
                this.filteredOptionsCurrentType =
                    fgroup.controls.currentType.valueChanges.pipe(
                        startWith(""),
                        map((value) =>
                            typeof value === "string" ? value : value.name
                        ),
                        map((name) =>
                            name
                                ? this._filterCurrentType(name)
                                : this.currentTypes
                        )
                    );
            },
            (err) => {}
        );
    }

    onSubmit() {
        if (this.form.valid) {
            let formValues = this.form.value;
            let connectors = [];

            formValues.connectorForm.forEach((item) => {
                connectors.push({
                    connector_type_id: item.connectorType,
                    io_type_id: item.ioType,
                    current_type_id: item.currentType,
                    voltage: item.voltage,
                    phase: item.phase,
                    max_amp: item.maxAmp,
                    power: item.power,
                    frequency: item.frequency,
                    status: this.status == true ? "Y" : "N",
                    created_by: this.localStorage.userId,
                });
            });

            let params: ChargingModel = {
                charger_type_id: formValues.chargerType.id,
                manufacturer_id: formValues.manufacturer.id,
                charger_model_type_id: formValues.chargerModelType.id,
                battery_backup: this.batteryBackup == true ? "Y" : "N",
                isDual: this.isDualCardReader == true ? 1 : 0,
                code: formValues.code,
                name: formValues.name,
                description: formValues.description,
                communication_protocol_id: formValues.communicationProtocol.id,
                communication_mode: formValues.communicationMode,
                card_reader_type: formValues.cardReaderType,
                no_of_connectors: formValues.connectorForm.length,
                connector_data: connectors,
                status: this.status == true ? "Y" : "N",
                created_by: this.localStorage.userId,
            };

            this.chargingModelService.create(params).subscribe(
                (resp: any) => {
                    if (resp != null) {
                        this.snackBar.open(
                            MessageConstant.chargerCreateMessage,
                            "Dismiss",
                            { duration: 5000 }
                        );
                        this.router.navigate([
                            "/assetManagement/chargingModel/crud/list",
                        ]);
                    }
                },
                (err) => {
                    this.snackBar.open(
                        MessageConstant.errorMessage,
                        "Dismiss",
                        { duration: 5000 }
                    );
                }
            );
        } else {
            this.snackBar.open(MessageConstant.formInvalid, "Dismiss", {
                duration: 5000,
            });
        }
    }

    abc() {
        this.form;
    }

    clkBatteryBackup(event) {
        this.batteryBackup = event.checked;
        this.ref.checked = this.batteryBackup;
    }

    clkRfidCardReader(event) {
        this.rfidCardReader = event.checked;
        this.rfid.checked = this.rfidCardReader;
    }

    clkIsDualCardReader(event) {
        this.isDualCardReader = event.checked;
        this.isDual.checked = this.isDualCardReader;
    }

    getErrorMessage(form: FormGroup) {
        return form.get("email").hasError("required")
            ? "You must enter a value"
            : form.get("email").hasError("email")
            ? "Not a valid email"
            : "";
    }

    filteredOptions() {
        this.filteredOptionsCharType =
            this.form.controls.chargerType.valueChanges.pipe(
                startWith(""),
                map((value) =>
                    typeof value === "string" ? value : value.name
                ),
                map((name) =>
                    name ? this._filterCharType(name) : this.chargerTypes
                )
            );
        this.filteredOptionsManufacturer =
            this.form.controls.manufacturer.valueChanges.pipe(
                startWith(""),
                map((value) =>
                    typeof value === "string" ? value : value.name
                ),
                map((name) =>
                    name ? this._filterManfacturer(name) : this.manufacturers
                )
            );
        this.filteredOptionsCharModelType =
            this.form.controls.chargerModelType.valueChanges.pipe(
                startWith(""),
                map((value) =>
                    typeof value === "string" ? value : value.name
                ),
                map((name) =>
                    name
                        ? this._filterCharModelType(name)
                        : this.chargerModelTypes
                )
            );
        this.filteredOptionsCommunicationProto =
            this.form.controls.communicationProtocol.valueChanges.pipe(
                startWith(""),
                map((value) =>
                    typeof value === "string" ? value : value.name
                ),
                map((name) =>
                    name
                        ? this._filterCommunicationProtocol(name)
                        : this.communicationProtocols
                )
            );
    }

    displayCharType(item: any): string {
        return item && item.name ? item.name : "";
    }

    displayManfacturer(item: any): string {
        return item && item.name ? item.name : "";
    }

    displayCharModelType(item: any): string {
        return item && item.name ? item.name : "";
    }

    displayCommunicationProto(item: any): string {
        return item && item.name ? item.name : "";
    }

    displayConnectorType(item: any): string {
        return item && item.name ? item.name : "";
    }

    displayIoTypeAuto(item: any): string {
        return item && item.name ? item.name : "";
    }

    displaycurrentType(item: any): string {
        return item && item.name ? item.name : "";
    }

    private _filterCharType(name: string): any[] {
        const filterValue = name.toLowerCase();
        return this.chargerTypes.filter((option) =>
            option.name.toLowerCase().includes(filterValue)
        );
    }

    private _filterManfacturer(name: string): any[] {
        const filterValue = name.toLowerCase();
        return this.manufacturers.filter((option) =>
            option.name.toLowerCase().includes(filterValue)
        );
    }

    private _filterCharModelType(name: string): any[] {
        const filterValue = name.toLowerCase();
        return this.chargerModelTypes.filter((option) =>
            option.name.toLowerCase().includes(filterValue)
        );
    }

    private _filterCommunicationProtocol(name: string): any[] {
        const filterValue = name.toLowerCase();
        return this.communicationProtocols.filter((option) =>
            option.name.toLowerCase().includes(filterValue)
        );
    }

    private _filterConnectorType(name: string): any[] {
        const filterValue = name.toLowerCase();
        return this.connectorTypes.filter((option) =>
            option.name.toLowerCase().includes(filterValue)
        );
    }

    private _filterIoType(name: string): any[] {
        const filterValue = name.toLowerCase();
        return this.ioTypes.filter((option) =>
            option.name.toLowerCase().includes(filterValue)
        );
    }

    private _filterCurrentType(name: string): any[] {
        const filterValue = name.toLowerCase();
        return this.currentTypes.filter((option) =>
            option.name.toLowerCase().includes(filterValue)
        );
    }

    updateStatus(event) {
        this.status = event.checked;
        this.active.checked = this.status;
    }
}
