import { NgModule} from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ChargingModelCrudComponent } from './charging-model-crud/charging-model-crud.component';
import { TABS_DEMO_ROUTES } from './charging-model-crud/routes';
import { AuthGuard } from 'app/shared/interceptors/auth-gaurd';
const routes : Routes = [
    {
        path : 'crud',component : ChargingModelCrudComponent,canActivate: [AuthGuard], children : TABS_DEMO_ROUTES
    }
]

@NgModule({
    imports : [RouterModule.forChild(routes)],
    exports : [RouterModule]
})

export class ChargingModelRoutingModule{}